@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
* {
  box-sizing: border-box;
  font-family: Montserrat;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: #333333;
}

::-moz-selection {
  background: #4e5d6c;
  color: white;
}

::selection {
  background: #4e5d6c;
  color: white;
}
